import React, { useState, useEffect } from 'react';
import { getArchivedNews } from '../apiConfig';
import { NewsListItem } from './NewsHighlights'; // Re-using the list item component

const DeeperDive = () => {
    const [articles, setArticles] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        const fetchNews = async () => {
            try {
                setLoading(true);
                const response = await getArchivedNews();
                if (response && Array.isArray(response.data)) {
                    setArticles(response.data);
                } else {
                    setArticles([]);
                }
                setError(null);
            } catch (err) {
                setError('Failed to load archived news.');
                console.error('Error fetching archived news:', err);
            } finally {
                setLoading(false);
            }
        };

        fetchNews();
    }, []);

    // Placeholder Div - Adjust min-height as needed based on typical content size
    const Placeholder = () => (
        <div className="mt-6 min-h-[300px]"> {/* Added min-height */}
            <h3 className="text-base font-bold text-gray-800 pb-2 mb-2 border-b-2 border-gray-300">
                Deeper Dive
            </h3>
            {/* Optional: Add skeleton loaders for better visual feedback */}
            <div className="space-y-4 pt-2">
                <div className="h-10 bg-gray-200 rounded animate-pulse"></div>
                <div className="h-10 bg-gray-200 rounded animate-pulse"></div>
                <div className="h-10 bg-gray-200 rounded animate-pulse"></div>
            </div>
        </div>
    );

    // Render placeholder if loading, error, or no articles
    if (loading || error || articles.length === 0) {
        // Return the placeholder instead of null to reserve space
        return <Placeholder />;
    }

    // Render content when available
    return (
        <div className="mt-6 min-h-[300px]"> {/* Keep min-height for consistency */}
            <h3 className="text-base font-bold text-gray-800 pb-2 mb-2 border-b-2 border-gray-300">
                Deeper Dive
            </h3>
            <div>
                {articles.map((item) => (
                    <NewsListItem
                        key={item.id}
                        title={item.title}
                        link={item.link}
                        source={item.source}
                        publishedAt={item.publishedAt}
                    />
                ))}
            </div>
        </div>
    );
};

export default DeeperDive;